import './style.css';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import fontVertexShader from './shader/text/vertex.glsl';
import fontFragmentShader from './shader/text/fragment.glsl';

global.THREE = THREE;
const createText = require('three-bmfont-text');

/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl');

// Scene
const scene = new THREE.Scene();

// Texture
const textureLoader = new THREE.TextureLoader();
const texture = textureLoader.load('/fonts/Roboto-Black.png');
scene.background = textureLoader.load(TEXTURE);

/**
 * Fonts
 */
const fontLoader = new THREE.FontLoader();
let material = null;

fontLoader.load('/fonts/Roboto-Black-msdf.json', (font) => {
  const geometry = createText({
    text: TEXT,
    font: font.data,
    align: 'center',
    width: 300,
    flipY: texture.flipY,
  });

  material = new THREE.ShaderMaterial({
    vertexShader: fontVertexShader,
    fragmentShader: fontFragmentShader,
    uniforms: {
      animate: { value: 1 },
      iGlobalTime: { value: 0 },
      map: { value: texture },
      color: { value: new THREE.Color('#fff') },
    },
    transparent: true,
    side: THREE.DoubleSide,
    depthTest: false,
  });

  const layout = geometry.layout;
  const text = new THREE.Mesh(geometry, material);
  text.scale.multiplyScalar(-0.01);
  text.position.x = (layout.width / 2) * 0.01;
  text.position.y = -(layout.height / 2) * 0.01;
  scene.add(text);
});
/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

window.addEventListener('resize', () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(
  45,
  sizes.width / sizes.height,
  0.1,
  100
);
camera.position.z = -2;
scene.add(camera);

// Controls
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  antialias: true,
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

/**
 * Animate
 */
const clock = new THREE.Clock();
let lastElapsedTime = 0;
let duration = 3;

const tick = () => {
  const elapsedTime = clock.getElapsedTime();
  const deltaTime = elapsedTime - lastElapsedTime;
  lastElapsedTime = elapsedTime;

  // Update controls
  controls.update();

  if (material) {
    material.uniforms.iGlobalTime.value = elapsedTime % duration;
    material.uniforms.animate.value = (elapsedTime % duration) / duration;
  }

  // Render
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();
export default screen;
